<template>
  <div>
    <h4 class="mdl-color-text--primary text-center">Sales Order</h4>
    <hr>
    <order-view
      :clientData="clients.data"
      :clientAction="fetchClients"
      :productData="products.data"
      :productAction="fetchProducts"
      :productFk="'product'"
      :clientFk="'client'"
      :order="orderParams"
      @orderComplete="orderParams=$event"
    ></order-view>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import { OrderView } from 'SHARED/orders'
export default {
  name: 'sales-order-view',
  components: {
    OrderView
  },
  computed: {
    ...mapGetters({
      clients: 'getClients',
      products: 'getProducts'
    }),
    orderParams: {
      get () {
        return this.order || this.params
      },
      set (data) {
        this.$set(this, 'params', data)
        console.log(this.params)
        this.createOrder()
      }
    }
  },
  methods: {
    ...mapActions([
      'fetchClients',
      'fetchProducts',
      'createSalesOrder'
    ]),
    createOrder () {
      this.createSalesOrder(this.orderParams).then(() => {
        this.$toastr.s('Successfully created')
        this.$emit('completed')
      }).catch(e => {
        this.$toastr.e('Please verify error')
      })
    }
  },
  data () {
    return {
      params: {
        client: undefined,
        name: '',
        address: '',
        line_items: [],
        transactions: [],
        sgst_percentage: 0,
        cgst_percentage: 0,
        igst_percentage: 0
      }
    }
  }
}
</script>



